<template>
  <div class="event_detail">
    <div class="event_detail_scroll">
      <cube-scroll :data="comments" :options="eventDetailScrollOptions" @pulling-up="eventDetailPullingUp()">
        <div class="group_info">
          <div class="left">
            <div class="members" >
              <img src="../../assets/touxiang.png">
              <img src="../../assets/touxiang.png">
              <img src="../../assets/touxiang.png">
            </div>
            <div style="margin-left: 0.24rem">
              <div><label>天生包子党！</label><label style="margin-left: 0.31rem">(99人)</label></div>
              <div style="font-size: 0.20rem;margin-top: 0.15rem">隧道标签</div>
            </div>
          </div>
          <label class="right" :class="{ right_active: isFollow }">{{isFollow ? '已加入' : '加入'}}</label>
        </div>

        <div class="event" >
          <div class="event_info">
            <div class="left">
              <img src="../../assets/touxiang.png">
              <div class="user_info" style="margin-top: 0.17rem;margin-left: 0.24rem">
                <div>包子大叔</div>
                <div style="font-size: 0.20rem;margin-top: 0.08rem">隧道主</div>
              </div>
            </div>
            <label style="font-size: 0.2rem;">1天前</label>
          </div>
          <div class="event_content three_line_text">Dota2 音乐会有参加的吗？一起的下方留言！地点：北京凯迪拉克中心网易2001年正式成立在线游戏事业部，经过近20年的快速发展，网易的品牌价值已超过13亿美元，并跻身全球七大游戏公司之一</div>
          <div class="event_spec bottom-line"></div>
          <label class="event_comment_count">评论 999k</label>
        </div>

        <div>
          <div class="event_comments" >
            <event-comment :comments="comments" :commentReplys="mydata.commentReplys"></event-comment>
          </div>
        </div>
      </cube-scroll>
    </div>

    <div class="my_comment">
      <cube-input class="input"></cube-input>
      <div class="zheanniushajibayisi">
        <img class="zan_comment" src="../../assets/huifu.png"><label> 999 </label>
        <img class="zan_comment" src="../../assets/dianzan.png"><label> 999</label>
      </div>
    </div>
    
  </div>
</template>

<script type="text/javascript">

  import EventComment from 'pages/event-comment.vue'
  import { scrollOptions } from 'common/js/util.js'


  export default {
    data () {
      return {
        isFollow: true,
        tabLabels: [{
          label: '详情'
        }, {
          label: '游戏'
        }, {
          label: '动态'
        }],
        comments: ['1', '2','3','2','3','2','3'],
        eventDetailScrollOptions: scrollOptions(false, true, true),
        mydata: {
          commentReplys: ['1']
        },
        abstract: '网易2001年正式成立在线游戏事业部，经过近20年的快速发展，网易的品牌价值已超过13亿美元，并跻身全球七大游戏公司之一。'
      }
    },
    methods: {
      goBack() {
        this.$router.back()
      },
      goToReplyDetail() {
        console.log("?????")
      },
      eventDetailPullingUp() {
        setTimeout( () => {
          this.comments = this.comments.concat(['1','2'])
        }, 1500)
      }

    },
    computed: {

    },
    components: {
      EventComment
    }
  }
</script>


<style scoped lang="stylus">
  @import "~common/stylus/variable"

  .event_detail
    background-color: #FFF
    color: $content-color
    font-size: 0.25rem
    .event_detail_scroll
      position: absolute
      top: 0
      left: 0
      right: 0
      bottom: 1.1rem
      .group_info
        display: flex
        justify-content: space-between
        align-items: center
        padding: 0.14rem 0.30rem 0.20rem 0.28rem
        background-color: #F7F7F7
        border-bottom: 1px solid $spec-color
        .left
          display: flex
          align-items: center
          .members
            width: 1.44rem
            height: 1.03rem
            position: relative
            img
              width: 0.72rem
              height: 0.72rem
            for n in 1 2 3
              img:nth-child({n})
                position: absolute
                if n < 3
                  top: 50%
                  transform: translate(80% * (n - 1), -50%)
                else
                  transform: translateX(40%)
        .right
          width: 1.49rem
          height: 0.5rem
          font-size: 0.20rem
          text-align: center
          line-height: 0.5rem
          background: rgba(252,246,246,1)
          border-radius: 5px
        .right_active
            color: white
            background-color: #FFA535
      .event
        position: relative
        padding: 0.34rem 0.42rem 0 0.34rem
        .event_info
          display: flex
          justify-content: space-between
          align-items: center
          .left
            display: flex
            img
              width: 0.72rem
              height: 0.72rem
        .event_content
          padding-left: 0.92rem
          padding-right: 0.79rem
          padding-top: 0.23rem
        .event_spec
           margin-top: 0.54rem
        .event_comment_count
          position: absolute
          top: 100%
          left: 0.56rem
          transform: translateY(-50%)
          font-size: 0.20rem
          background-color: white
      .event_comments
        padding: 0.31rem 0.19rem 0
        // &:nth-of-type(1)
        //   margin-bottom: 1.23rem
    .my_comment
      position: fixed
      left: 0
      bottom: 0
      width: 100%
      height: 1.10rem
      box-sizing: border-box
      border-top: 0.02rem solid $spec-color
      padding: 0.19rem 0.33rem 0.21rem 0.24rem
      display: flex
      align-items: center
      background-color: #FFF
      .input
        width: 4.77rem
      .zheanniushajibayisi
        margin-left: 0.24rem
        font-size: 0.20rem
    
  
</style>


